@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;600&display=swap');

$transition-time: 2.3s;

body {
  font-family: 'Oswald', sans-serif;
  background-color: #212121;

  section {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;

    .row {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      gap: 20px;
      justify-content: center;
      align-items: center;
    }
  }
}

.card {
  position: relative;
  height: 400px;
  width: 100%;
  transition: ease all $transition-time;
  perspective: 1200px;

  &:hover {
    .cover {
      transform: rotateX(0deg) rotateY(-180deg);

      h1 {
        transform: translateZ(100px);
      }

      .price {
        transform: translateZ(60px);
      }
    }
  }

  .cover {
    position: absolute;
    height: 100%;
    width: 100%;
    transform-style: preserve-3d;
    transition: ease all $transition-time;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

    &.item-1 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+1');
    }

    &.item-2 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+2');
    }

    &.item-3 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+3');
    }

    &.item-4 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+4');
    }

    &.item-5 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+5');
    }

    &.item-6 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+6');
    }

    &.item-7 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+7');
    }

    &.item-8 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+8');
    }

    &.item-9 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+9');
    }

    &.item-10 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+10');
    }

    &.item-11 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+11');
    }

    &.item-12 {
      background-image: url('https://via.placeholder.com/300x400?text=Item+12');
    }

    h1 {
      font-weight: 600;
      position: absolute;
      bottom: 55px;
      left: 20px;
      color: white;
      z-index: 3;
      font-size: 1.5em;
      transform: translateZ(0px);
    }

    .price {
      font-weight: 200;
      position: absolute;
      top: 20px;
      right: 20px;
      color: white;
      z-index: 4;
      font-size: 1.3em;
      transform: translateZ(0px);
    }
  }

  .card-back {
    position: absolute;
    height: 100%;
    width: 100%;
    background: #0b0f08;
    transform-style: preserve-3d;
    transition: ease all $transition-time;
    transform: translateZ(-1px);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    text-align: center;

    p {
      color: white;
      margin-bottom: 20px;
      font-size: 1em;
    }

    a {
      transform-style: preserve-3d;
      transition: ease background 0.5s;
      background: white;
      color: #0b0f08;
      padding: 10px 20px;
      text-decoration: none;
      font-size: 1em;

      &:hover {
        background: #f0f0f0;
        color: black;
      }
    }
  }
}